	import ePicker from '@/components/e-picker/e-picker.vue';
	import myPickerPart from "@/components/myPickerPartProps.vue";
	import uniIcon from "@/components/uni-icons/uni-icons.vue";
	let _this;
	function getDate(type) {
		const date = new Date();
	    date.setTime(date.getTime()+24*60*60*1000);
		let year = date.getFullYear();
		let month = date.getMonth() + 1;
		let day = date.getDate();
	    let hour = date.getHours();
		let minute = date.getMinutes()
		let second = date.getSeconds()
		
		if (type === 'start') {
			year = year - 60;
		} else if (type === 'end') {
			year = year + 2;
		}
		month = month > 9 ? month : '0' + month;;
		day = day > 9 ? day : '0' + day;
		hour = hour > 9 ? hour : '0' + hour;
		minute = minute > 9 ? minute : '0' + minute;
		second = second > 9 ? second : '0' + second;
	
		return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
	}
	export default {
		components:{
			myPickerPart,
			uniIcon,
			ePicker
		},
		data() {
			return {
				purcharseArr:[],
				
				remarksArrs:[],
				form:{
					buyOrSellCode: '',      //联系人编码
					
					remarks:'',				//备注
					nextTime:'',		    //下次跟进时间
					nextRemarks:'',			//下次跟进备注
					
				},
				date: getDate({
					format: true
				})
				
			};
		},
		onLoad:function(options){
			_this = this
			this.form = JSON.parse(options.item)
			let tmp = ''
			//内容拼接
			this.remarksArrs = this.form.remarks
			this.form.remarks.forEach((item)=>{
				tmp += `${item.name}：${item.remarks}\n`
			})
			this.form.remarks = tmp
			this.date = this.form.nextTime+':00'
            this.getType()
		},
		methods:{
			async getType(){
				const res = await this.$http.get('/choose/remarks_type',{})
				this.purcharseArr = res.data.data.list
				this.purcharseArr.forEach((item)=>{
					this.remarksArrs.forEach((subItem)=>{
						if (subItem.code == item.id){
							this.$set(item,'isChecked',true)
						}
					})
				})
				console.log(res)
			},
			handleClick:function(e){
				
				let reg = new RegExp(e.label)
				
				if (!reg.test(this.form.remarks)){
					
					this.purcharseArr.forEach((item)=>{
						if (item.id == e.id){
							this.$set(item,'isChecked',!item.isChecked)
						}
					})
					if (e.isChecked){
						if (this.form.remarks.length > 0){
							this.form.remarks = this.form.remarks + `\n${e.label}：`
						}else{
							this.form.remarks = this.form.remarks + `${e.label}：`
						}
					}
					
				}
	
			},
			bindDateChange: function(e) {
				this.date = e.target.value
			},
			confirm(obj){
				console.log(obj)
				this.date = obj.selectTime
			},
			
			areaInput:function(e){
				console.log(e)
			},
			
			async submit(){
				
				this.form.nextTime = this.date
				// this.form.remarks = this.form.remarks.replace(/\n/g, '')
				let data=this.form
                console.log(data)
				
				const res = await this.$http.post('/portrayal/update',{data:data})
				console.log(res)
				uni.navigateBack({
					delta: -1
				});
				// var pages = getCurrentPages();
				// var currPage = pages[pages.length - 1]; //当前页面
				// var prevPage = pages[pages.length - 2]; //上一个页面
				// //直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				// prevPage.setData({
				//    isDoRefresh:true
				// })
				
				
			}
		},
		
	}
